@import url(https://fonts.googleapis.com/css?family=Roboto:400,500,700&display=swap);
.Label {
  color: var(--gray-100);
  border-radius: 3px;
  padding: 3px 6px;
  font-size: 12px; }

.Avatar {
  width: 60px;
  border-radius: 50%; }

.SideNav {
  height: 100%;
  background-color: var(--blue-500);
  color: var(--blue-100); }
  .SideNav .user-details {
    padding: 20px 10px 10px 20px; }
    .SideNav .user-details .user-role-dropdown {
      display: flex;
      align-items: center;
      margin-top: 15px; }
      .SideNav .user-details .user-role-dropdown .anticon {
        margin-left: 15px;
        font-size: 12px; }
  .SideNav nav ul.menu li.menu-item {
    padding: 20px 10px 20px 20px;
    font-weight: 500;
    font-size: 14px;
    cursor: pointer; }
    .SideNav nav ul.menu li.menu-item .menu-item-head {
      display: flex;
      align-items: center; }
      .SideNav nav ul.menu li.menu-item .menu-item-head .anticon.item-icon {
        font-size: 14px;
        position: relative;
        top: 1px;
        margin-right: 15px; }
      .SideNav nav ul.menu li.menu-item .menu-item-head .item-text {
        flex-grow: 1; }
      .SideNav nav ul.menu li.menu-item .menu-item-head .item-caret {
        font-size: 10px;
        margin-left: 5px; }
    .SideNav nav ul.menu li.menu-item .menu-item-body ul.submenu {
      padding-top: 20px; }
      .SideNav nav ul.menu li.menu-item .menu-item-body ul.submenu li.submenu-item {
        padding: 10px 20px 10px 40px;
        color: var(--blue-100); }
        .SideNav nav ul.menu li.menu-item .menu-item-body ul.submenu li.submenu-item:hover {
          color: var(--gray-100); }
    .SideNav nav ul.menu li.menu-item.selected {
      color: var(--gray-100);
      background-color: var(--turkoise); }
    .SideNav nav ul.menu li.menu-item.open {
      color: var(--gray-100);
      background-color: var(--blue-700);
      border-left: 2px solid var(--turkoise); }

.card {
  background-color: var(--white);
  border-top: 3px solid var(--gray-200); }
  .card .card-header {
    display: flex;
    justify-content: space-between;
    padding: 10px 15px;
    border-bottom: 1px solid var(--gray-200);
    font-size: 12px;
    font-weight: 500; }
    .card .card-header .action-buttons .anticon {
      margin: 0 5px;
      cursor: pointer; }
      .card .card-header .action-buttons .anticon:last-child {
        margin-right: 0; }
  .card .card-body {
    padding: 20px; }

.card.status-card {
  flex-basis: 24%; }
  .card.status-card .card-body .value {
    font-size: 36px;
    line-height: 1.5; }
  .card.status-card .card-body footer {
    display: flex;
    justify-content: space-between; }

.progress.progress-outer {
  height: 5px;
  border-radius: 2.5px;
  overflow: hidden;
  background-color: var(--gray-200);
  margin: 10px 0; }
  .progress.progress-outer .progress-inner {
    height: 100%; }

.graph-card .card-body {
  display: flex; }
  .graph-card .card-body .left {
    flex-grow: 1; }
    .graph-card .card-body .left img {
      width: 100%; }
  .graph-card .card-body .right {
    flex-basis: 25%;
    min-width: 250px;
    padding: 0 20px; }
    .graph-card .card-body .right ul li {
      padding: 15px 0; }
      .graph-card .card-body .right ul li .value {
        font-size: 28px;
        margin-bottom: 5px; }
      .graph-card .card-body .right ul li .label {
        font-size: 12px; }
      .graph-card .card-body .right ul li .percentage-value {
        font-size: 12px; }
        .graph-card .card-body .right ul li .percentage-value .anticon {
          margin-left: 5px;
          position: relative;
          top: 2px; }

.messages-card .card-body .notifications {
  margin: -20px;
  padding: 30px 20px;
  border-bottom: 1px solid var(--blue-100);
  background-color: var(--light-blue); }

.messages-card .card-body ul {
  margin: 20px 20px 20px 20px; }
  .messages-card .card-body ul li {
    padding: 25px 0;
    border-bottom: 1px solid var(--gray-200); }
    .messages-card .card-body ul li header {
      margin-bottom: 5px; }
      .messages-card .card-body ul li header .name {
        font-weight: bold; }
      .messages-card .card-body ul li header .time-ago {
        font-size: 12px; }
    .messages-card .card-body ul li footer {
      margin-top: 5px;
      font-size: 13px;
      color: var(--gray-600); }

.App {
  display: flex;
  align-items: stretch;
  height: 100%; }
  .App > .left {
    flex-basis: 220px;
    flex-shrink: 0; }
  .App > .right {
    flex-grow: 1; }
  .App .main-content {
    padding: 0 25px; }
  .App .status-cards-container {
    display: flex;
    justify-content: space-between; }

:root {
  --white: #ffffff;
  --gray-100: #f3f3f4;
  --gray-200: #e6e9eb;
  --gray-600: #a2a2a2;
  --gray-800: #737373;
  --black: #1F1F1F;
  --light-blue: #f3f6fb;
  --blue-100: #9aa6b6;
  --blue-500: #2f4050;
  --blue-700: #293846;
  --turkoise: #1cc09f;
  --orange-100: #f8ac59;
  --java: #23c6c8;
  --java-500: #1c84c6; }

html, body, #root {
  height: 100%; }

html {
  display: table; }

body {
  font-family: 'Roboto', sans-serif;
  background-color: #f3f3f4;
  background-color: var(--gray-100);
  color: #737373;
  color: var(--gray-800); }

p {
  line-height: 1.3; }

.row {
  margin: 30px 0; }

.flex {
  display: flex; }

.flex-space-between {
  justify-content: space-between; }

